import React, { useState, useEffect, useContext } from 'react';

import classes from './styles/History.module.css';

import HistoryItem from '../../components/HistoryItem/HistoryItem';
import LoadingSpinner from '../../components/UI/LoadingSpinner/LoadingSpinner';

import historyService from '../../service/history';
import AuthContext from '../../context/authContext';

function History() {
  const [paths, setPaths] = useState([]);

  const authContext = useContext(AuthContext);

  useEffect(() => {
    console.log(authContext.loggedUser);
    authContext.loadingState.setLoading(true);
    historyService(authContext.loggedUser).then(res => {
      setPaths([...res]);
      authContext.loadingState.setLoading(false);
    });
  }, []);

  return (
    <div className={classes.History}>
      <h1>Your request History</h1>
      {authContext.loadingState.loading && <LoadingSpinner />}
      {!authContext.loadingState.loading && (
        <ul className={classes['history-list']}>
          {paths.map((path, index) => {
            return <HistoryItem key={index} path={path} />;
          })}
        </ul>
      )}
    </div>
  );
}

export default History;
